
nav.Navbar {
    box-shadow: 1px 1px 5px 0px #e4e4e4e0;
    z-index: 999;
    margin-bottom: 1%;
}
.SubHeaderRow {
    display: grid;
    grid-template-columns: 13% 17% 70%;
    align-items: center;
    padding: 0.5% 0;
}
.sub_header{
    background-color: var(--GlbBlue);
}
.SubHeaderRow.container div .btn-text svg {
    font-size: 20px;
}
.icon-btn-text a i {
    color: #fff;
    font-size: 16px;
}
.SubHeaderRow > .SubHeaderCol2{
    padding-left: 10%;
    border-left: 1px solid #fff;
}
.icon-btn-text {
    display: flex;
    gap: 15px;
}
.SubHeaderRow.container div .btn-text {
    display: flex;
    cursor: pointer;
    gap: 10px;
    align-items: center;
    color: #ffff;
    font-family: 'NanumGothic';
    font-weight: 300;
    font-size: 14px;
}
li.vl {
    border: 1px solid #fff;
    transform: rotate(15deg);
}
.SubHeader-LG-Sig ul a li button:hover, .SubHeader-LG-Sig ul a li button:focus, .SubHeader-LG-Sig ul a li button:active {
    color: var(--Orange );
}
.SubHeader-LG-Sig ul a li button {
    background-color: transparent;
    border: none;
    color: #fff;
    font-family: 'NanumGothic';
    font-weight: 400;
    font-size: 14px;
    cursor: pointer;
}
.SubHeader-LG-Sig ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}
.P_Header_block {
    background-color: #0000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1% 0;
}
.P_Header_block .Menu_List ul.Menu_Ul_List {
    display: flex;
    gap: 35px;
    align-items: center;
    padding: auto;
    margin: 0;
}
.P_Header_block .Menu_List ul.Menu_Ul_List li {
    list-style: none;
}
.P_Header_block .Menu_List ul.Menu_Ul_List li a {
    text-decoration: none;
    font-size: 18px;
    color: #474C54;
    font-weight: 400;
}
.P_Header_block .Menu_List ul.Menu_Ul_List li a:hover, .P_Header_block .Menu_List ul.Menu_Ul_List li a:focus {
    color: var(--Orange);
}
.P_Header_block .Menu_List ul.Menu_Ul_List li button {
    padding: 0;
    background-color: transparent;
    border: none;
}

.P_Header_block .Menu_List ul.Menu_Ul_List li button a {
    font-size: 16px;
    font-family: 'NanumGothic';
}
.P_Header_block .Menu_List ul.Menu_Ul_List li button.Sign-up-btn {
    padding: 12px 25px;
    background-color: var(--GlbBlue);

}
.P_Header_block .Menu_List ul.Menu_Ul_List li button.Sign-up-btn > a {
    color: #fff;
    font-weight: 400;
}
.Public_Header.container {
    /* position: absolute; */
    width: 100%;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 0;
    padding: 0 0;
    z-index: 99;
}
a.HS-Left-Mg {
    padding-left: 35px;
    border-left: 1px solid #ffffff75;
}

.link_is_.active {
    color: var(--Orange) !important;
}
.btn-text i.fa-light.fa-envelope {
    margin-top: 3px;
}
