footer.Hs-Stickey-Footer {
    background-color: var(--GlbBlue);
}

.hs-column-wrap.hs-elements > p{
    padding: 0;
    margin: 0;
    padding-top: 5%;
    color: rgba(255, 255, 255, 1);
    font-weight: 300;
    font-size: 16px;
    font-family: 'NanumGothic';
    line-height: 1.8em;
    padding-right: 30%;
}
.Ftr-row.container {
    display: grid;
    grid-template-columns: 46% 18% 18% 18%;
    padding: 4rem 0;
}
.hs-column-wrap.hs-elements > h1 {
    font-size: 25px;
    padding: 0;
    margin: 0;
    color: #fff;
    font-family: 'Larken';
    font-weight: 500;
    padding-bottom: 5%;
}
.hs-ftr-col.first {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.hs-ftr-col > .hs-column-wrap.hs-elements ul a {
    text-decoration: none;
    color: #fff;
}
.hs-ftr-col > .hs-column-wrap.hs-elements ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    padding-top: 3rem;
    color: rgba(255, 255, 255, 1);
    font-weight: 300;
    font-size: 16px;
    font-family: 'NanumGothic';
}
.btn-text-ft{
    display: flex;
    gap: 0.9rem;
    text-decoration: none;
    color: #fff;
}
.btn-text-ft a {
    display: flex;
    gap: 0.9rem;
    text-decoration: none;
    color: #fff;
}
.icon-btn-text-scl-ic{
        display: flex;
        gap: 17px;
        padding-top: 4%;
}
.icon-btn-text-scl-ic a i {
    color: #fff;
    font-size: 16px;
}
.hs-sub-header-row {
    display: flex;
    justify-content: space-between;
    color: #fff;
    font-size: 14px;
    font-family: 'NanumGothic';
}
.hs-sub-header-col-forsep {
    display: flex;
    gap: 3rem;
}
.hs-sub-footer {
    padding: 1rem 0;
    border-top: 1px solid #c7c5c5a2;
}
.hs_col_sun_col_2 > span, .hs_col_sun_col_3 > span {
    color: #c7c5c5;
}
