section.first-hs-banner-section{
    /* background-image: url("http://localhost:3000/static/media/HomeBanner.21b501e950d64f00297f.png"); */
    background-repeat: no-repeat;
    object-fit: cover;
    background-size: 100%;
    background-color: #fff;
}


section .Banner_Section {
    display: flex;
    align-items: center;
    color: #fff;
    height: 65vh;
    justify-content: center;
    position: relative;
}
.Hs-slider-Img {
    margin-right: 3%;
}
.HS-Bnr-Title {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

section .Banner_Section.container .HS-Bnr-Title h1 {
    font-size: 55px;
    font-family: 'Larken';
    text-align: left;
    margin: 0;
    color: var(--GlbBlue);
}

section .Banner_Section.container .HS-Bnr-Title p {
    margin: 0;
    font-size: 20px;
    text-align: left;
    color: var(--GlbBlue);
    padding-right: 35%;
    line-height: 1.7em;
}

.Banner_Section.container .HS-Bnr-Title h1 > b.styles_typicalWrapper__1_Uvh {
    font-weight: 500;
    color: var(--Orange);
}

.Sticy-HS-Btn {
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 30px;
    width: 100%;
}

.Sticy-HS-Btn .btn-text{
    display: flex;
    cursor: pointer;
    gap: 10px;
    align-items: center;
    color: #7D8288;
}

.Sticy-HS-Btn .icon-btn-text {
    display: flex;
    gap: 30px;
}
.Sticy-HS-Btn .icon-btn-text a > img {
    width: 20px;
    height: 20px;
    object-fit: contain;
}
.Home_Page {
    background-color: var(--BgGray);
}
section.hs-second-slider-section{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    
    color: #000;
    height: auto;
    justify-content: center;
    position: relative;
    padding: 7% 0;
}
section.hs-thired-slider-section{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: #fff;
    color: #000;
    height: auto;
    justify-content: center;
    position: relative;
    padding: 7% 0;
}

/* slider 1*/
.hs-slider-textual {
    text-align: left;
    padding: 1.8rem 1.4rem;
    margin-right: 6%;
    background-color: #fff;
    cursor: -webkit-grab;
    width: 100%;
    margin-bottom: 5%;
    margin-top: 2%;
    transition: 0.3s ease-in-out;
}
.hs-slider-textual:hover, .hs-slider-textual:focus{
    box-shadow: 6px 7px 7px 0px #bebebe54;
    transition: 0.2s ease-in-out;
}
.hs-slider-textual.profile {
    background-color: white;
}
.hs-slider-textual .Hs-cst-slider h1 {
    margin: 0;
    font-family: 'Larken';
    font-weight: 500;
    color: #3F3D56;
    font-size: 20px;
    letter-spacing: 1px;
}
.hs-slider-textual .Hs-cst-slider p {
    font-size: 16px;
    line-height: 26px;
    color: #7D8282;
    font-family: 'NanumGothic';
    padding-bottom: 7%;
    padding-top: 1%;
}
.hs-cmpn-lsc {
    display: flex;
    flex-direction: column;
    margin: 0;
    gap: 0.5rem;
}
.hs-cmpn-lsc > p {
    font-family: 'NanumGothic';
    color: #3F3D56;
    font-size: 15px;
    font-weight: bold;
    margin: 0;
    font-style: normal;
}
.hs-cmpn-lsc > span {
    font-family: 'NanumGothic';
    color: #7D8282;
    font-size: 14px;
    font-weight: bold;
    margin: 0;
    font-style: normal;
}
button.sc-bcXHqe.kXteup.rec.rec-arrow.rec.rec-arrow-left {
    position: absolute;
    top: -90px;
    right: 105px;
}

button.sc-bcXHqe.kXteup.rec.rec-arrow.rec.rec-arrow-right {
    position: absolute;
    top: -90px;
    right: 40px;
}
.rec-arrow-left.Slxdj:disabled{
    position: absolute;
    top: -90px;
    right: 105px;
}
.rec-arrow-right.Slxdj:disabled{
    position: absolute;
    top: -90px;
    right: 40px;
}
.hs-project-title > h1 {
    font-size: 38px;
    margin: 0;
    font-family: 'Larken';
    font-weight: 500;
    color: #3F3D56;
}
.sc-jSUZER.dQEjUh.rec.rec-carousel-wrapper {
    position: relative;
}
.hs-project-title {
    padding-bottom: 3%;
}
.kXteup:hover:enabled, .kXteup:focus:enabled {
    color: #fff;
    background-color: var(--GlbBlue) !important;
    box-shadow: 0 0 2px 0 #333;
}
section .hs-emplyee-profile_Section {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-start;
    align-items: flex-start;
}

/* Slider 2 */
.hs-img-icon-bx {
    display: flex;
    gap: 1.5rem;
}
.hs-profile-icon h1, .hs-profile-icon p {
    padding: 0;
    margin: 0;
}

.hs-profile-icon h1 {
    color: var(--SldTextClr);
    font-size: 20px;
    font-family: 'Larken';
    font-weight: 500;
}
.hs-fls-rtng {
    display: flex;
    gap: 2px;
    margin-top: -7%;
}

.hs-fls-rtng > p {
    color: var(--DarkRating);
}
.hs-profile-icon p {
    font-size: 14px;
}
.hs-img-icon-bx > img {
    object-fit: cover;
    width: 30%;
}
.hs-fls-rtng span {
    font-size: 18px;
}
.hs-profile-icon {
    display: flex;
    gap: 15px;
    flex-direction: column;
}
.hs-profile-icon > img {
    width: 28px;
    height: 28px;
}
.Hs-cst-slider.profile-title {
    padding: 8% 0 5% 0;
}
.Hs-cst-slider.profile-title > p {
    font-size: 16px;
    line-height: 26px;
    color: var(--NavText);
    font-family: 'NanumGothic';
    padding-bottom: 7%;
    padding-top: 2%;
    margin: 0;
}
.Hs-cst-slider.profile-title > span{
    font-size: 16px;
    line-height: 26px;
    color: var(--GlbBlue);
    font-family: 'NanumGothic';
    
    margin: 0;
}
.hs-done-p > p {
    font-size: 18px;
    line-height: 26px;
    color: var(--DarkRating);
    font-family: 'Larken';
    padding-top: 2%;
    margin: 0;
}
.hs-done-p {
    display: flex;
    align-items: flex-end;
}
.hs-done-p > h1 {
    margin: 0;
    font-family: 'Larken';
    font-weight: 500;
    color: var(--GlbBlue);
    font-size: 25px;
    letter-spacing: 1px;
}
.hs-done-project-view {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.hs-viewmore {
    cursor: col-resize;
    width: 50%;
    text-align: end;
}
.hs-viewmore.project {
    width: 40%;
}
.hs-viewmore.project a button{
    padding: 10% 15%;
}
.hs-viewmore a button {
    padding: 7% 15%;
    background-color: rgba(0, 74, 100, 0.15);
    color: var(--GlbBlue);
    border: none;
    font-size: 15px;
    font-family: 'NanumGothic';
    font-weight: 400;
    cursor: pointer;
    transition: 0.4s ease-in-out;
}
.hs-viewmore a button:hover, .hs-viewmore a button:focus{
    background-color: var(--GlbBlue);
    color: #fff;
    transition: 0.4s ease-in-out;
}
.hs-viewmore.extra_hs {
    width: 20%;
}
.hs-viewmore.extra_hs a {
    text-decoration: none;
}
.hs-done-p.ha-projects > p {
    font-size: 16px;
}

.hs-done-p.ha-projects > h1 {
    font-size: 20px;
}
.hs-viewmore.extra_hs a button {
    padding: 5% 15%;
    text-decoration: none;
    background-color: var(--GlbBlue);
    color: #fff;
    border: none;
    border-radius: 0px;
    font-size: 15px;
    font-family: 'NanumGothic';
    font-weight: 400;
    cursor: pointer;
    transition: 0.4s ease-in-out;
}
.title-button {
    width: 100%;
    justify-content: space-between;
    display: flex;
    align-items: flex-end;
    padding-bottom: 4%;
}
.hs-project-title.sec_three {
    padding: 0;
}
.hsicontextitem .clss p {
    padding: 0;
    margin: 0;
}

.hsicontextitem .clss h1 {
    margin: 0;
    padding: 0;
    font-family: 'Larken';
    font-size: 22px;
    font-weight: 500;
    color: var(--SldTextClr);
}
.hsicontextitem {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    padding: 20px 40px;
    align-items: center;
    background-color: #fff;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}
.hsicontextitem:hover, .hsicontextitem:focus{
    box-shadow: 6px 7px 7px 0px #bebebe54;
    transition: 0.2s ease-in-out;
    transform: translateY(-10px);
}

.hsicontextitem img {
    width: 33%;
    object-fit: contain;
    height: 85%;
}
.hsicontextitem .clss {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
}
.Hs-tech-griditems {
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: 2rem;
    width: 100%;
}
.hsicontextitem .clss p {
    font-size: 16px;
    font-family: 'NanumGothic';
    color: var(--DarkRating);
}
ul.slills-list {
    list-style: none;
    padding: 0;
    display: flex;
    gap: 8px;
}

ul.slills-list > li {
    background-color: rgb(230 120 41 / 20%);
    padding: 4px 10px;
    font-size: 14px;
    color: var(--GlbBlue);
}

/*  */
.hs-slider-textual-3 {
    text-align: left;
    padding: 1.8rem 1.4rem;
    margin-right: 6%;
    background-color: var(--SldCardClr);
    cursor: auto;
    width: 100%;
    margin-bottom: 5%;
    margin-top: 2%;
    transition: 0.3s ease-in-out;
    
}
.hs-slider-textual-3:hover, .hs-slider-textual-3:focus{
    box-shadow: 6px 7px 7px 0px #bebebe54;
    transition: 0.2s ease-in-out;
}
.hs-slider-icon-text {
    display: flex;
    gap: 2rem;
    padding: 5% 0 0;
}

.hs-slider-icon-text div {
    display: flex;
    align-items: center;
    gap: 0.8rem;
}

.hs-slider-icon-text div > p {
    padding: 0 !important;
    margin: 0;
}
ul.slills-list.projects {
    padding: 0;
    margin-bottom: 8%;
}
.Hs-bid-count > p {
    padding: 0;
    margin: 0;
    color: var(--DarkRating);
}
.Hs-bid-count {
    padding-top: 3%;
}
.rec.rec-carousel-item.rec-carousel-item-visible{
    transition: 0.4s ease-in-out;
}
.rec.rec-carousel-item.rec-carousel-item-visible:hover, .rec.rec-carousel-item.rec-carousel-item-visible:focus {
    transform: translateY(-20px);
    z-index: 1;
    transition: 0.4s ease-in-out;
}
/* Hidden items */
.sc-iBYQkv.jtKTCe.rec.rec-pagination {
    display: none;
}

/* Fourth section */
section.hs-fourth-slider-section{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: #fff;
    color: #000;
    height: auto;
    justify-content: center;
    position: relative;
    padding: 7% 0;
}
.hs_blog_cnt.container {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 1.5rem;
}
.Hs-blog-textual-cnt {
    padding: 0;
    margin: 0;
}
.Hs-blogs {
    display: flex;
    flex-direction: column;
}
.Hs-blogs > img {
    width: fit-content;
    height: 280px;
    object-fit: cover;
    width: 100%;
}
.Hs-blog-textual-cnt > p{
    padding: 0;
    margin: 0;
    padding-top: 5%;
    font-size: 16px;
    font-family: 'NanumGothic';
    font-weight: 500;
    line-height: 1.8em;
    color: var(--NavText);
}
.Hs-blog-textual-cnt > h1 {
    padding: 0;
    margin: 0;
    padding-top: 5%;
    font-size: 20px;
    font-family: 'Larken';
    font-weight: 500;
    color: var(--SldTextClr);
}
.Hs-Blog-Section > a {
    text-decoration: none;
    text-decoration-line: none;
    text-decoration-thickness: initial;
    text-decoration-style: initial;
    text-decoration-color: initial;
}
.Hs-blog-textual-cnt > span {
    display: inline-block;
    padding-top: 2%;
    font-size: 15px;
    color: var(--DarkRating);
    font-family: 'NanumGothic';
}
.Hs-blog-textual-cnt {
    padding: 0;
    margin: 0;
    background-color: var(--SldCardClr);
    padding: 0 6% 5%;
}
.Hs-Blog-Section{
    transition: 0.4s ease-in-out;
}
.Hs-Blog-Section:hover, .Hs-Blog-Section:focus {
    /* transform: translateY(-20px); */
    transition: 0.4s ease-in-out;
    cursor: pointer;
    box-shadow: 6px 7px 7px 0px #bebebe54;
}
